import gsap from 'gsap';
import { ScrollTrigger } from 'gsap/ScrollTrigger';
import { CustomEase } from 'gsap/CustomEase';

gsap.registerPlugin(ScrollTrigger, CustomEase);

interface AnimationState {
  energy: number;
  activeItems: Set<string>;
  timeline: gsap.core.Timeline;
  ripples: Map<
    string,
    {
      x: number;
      y: number;
      strength: number;
      timestamp: number;
    }
  >;
  spatialGrid: Map<string, Set<string>>;
  colorEnergy: number;
}

interface Point {
  x: number;
  y: number;
}

export class AnimationSystem {
  private static instance: AnimationSystem;
  private state: AnimationState;
  private readonly MAX_ENERGY = 100;
  private readonly MIN_ENERGY = 20;
  private readonly ENERGY_DECAY_RATE = 0.95;
  private readonly ENERGY_BOOST = 25;
  private readonly RIPPLE_DURATION = 1.5;
  private readonly BREATHING_INTENSITY = 0.02;
  private readonly GRID_SIZE = 200; // pixels per grid cell
  private readonly MAX_RIPPLES = 5;
  private readonly RIPPLE_LIFETIME = 2000; // milliseconds

  private constructor() {
    this.state = {
      energy: this.MIN_ENERGY,
      activeItems: new Set(),
      timeline: gsap.timeline(),
      ripples: new Map(),
      spatialGrid: new Map(),
      colorEnergy: 0,
    };

    this.startEnergyDecay();
    this.initializeCustomEases();
  }

  private initializeCustomEases() {
    CustomEase.create(
      'rippleOut',
      'M0,0 C0.126,0.382 0.282,0.674 0.44,0.822 0.632,1.002 0.818,1 1,1'
    );
    CustomEase.create(
      'energyPulse',
      'M0,0 C0.39,0 0.575,0.565 0.669,0.782 0.762,1 0.846,1 1,1'
    );
  }

  static getInstance(): AnimationSystem {
    if (!AnimationSystem.instance) {
      AnimationSystem.instance = new AnimationSystem();
    }
    return AnimationSystem.instance;
  }

  private startEnergyDecay() {
    gsap.ticker.add(() => {
      this.state.energy = Math.max(
        this.MIN_ENERGY,
        this.state.energy * this.ENERGY_DECAY_RATE
      );

      // Clean up old ripples
      const now = Date.now();
      for (const [id, ripple] of this.state.ripples.entries()) {
        if (now - ripple.timestamp > this.RIPPLE_LIFETIME) {
          this.state.ripples.delete(id);
        }
      }
    });
  }

  addEnergy(amount: number = this.ENERGY_BOOST) {
    this.state.energy = Math.min(this.MAX_ENERGY, this.state.energy + amount);
  }

  getEnergy(): number {
    return this.state.energy;
  }

  getAnimationProperties(itemId: string): gsap.TweenVars {
    const energy = this.state.energy;
    const isActive = this.state.activeItems.has(itemId);

    return {
      duration: 0.3 + (1 - energy / this.MAX_ENERGY) * 0.7,
      ease: energy > 70 ? 'elastic.out(1, 0.3)' : 'power2.out',
      transformOrigin: 'center center',
      // Add subtle movement based on energy
      scale: 1 + (energy / this.MAX_ENERGY) * 0.05,
      rotation: isActive
        ? (Math.random() > 0.5 ? 1 : -1) * (energy / this.MAX_ENERGY) * 2
        : 0,
    };
  }

  private calculateRippleEffect(element: HTMLElement): {
    scale: number;
    rotation: number;
    blur: number;
    brightness: number;
    translateZ: number;
  } {
    const rect = element.getBoundingClientRect();
    const elementCenter = {
      x: rect.left + rect.width / 2,
      y: rect.top + rect.height / 2,
    };

    let totalEffect = {
      scale: 1,
      rotation: 0,
      blur: 0,
      brightness: 1,
      translateZ: 0,
    };

    // Calculate combined effect of all active ripples
    for (const ripple of this.state.ripples.values()) {
      const distance = Math.hypot(
        elementCenter.x - ripple.x,
        elementCenter.y - ripple.y
      );

      const maxDistance = Math.max(window.innerWidth, window.innerHeight) * 0.5;
      const normalizedDistance = Math.max(0, 1 - distance / maxDistance);
      const rippleAge = (Date.now() - ripple.timestamp) / this.RIPPLE_LIFETIME;
      const rippleStrength =
        ripple.strength * (1 - rippleAge) * normalizedDistance;

      totalEffect.scale += rippleStrength * 0.1;
      totalEffect.rotation +=
        rippleStrength * 5 * (Math.random() > 0.5 ? 1 : -1);
      totalEffect.blur += rippleStrength * 2;
      totalEffect.brightness += rippleStrength * 0.1;
      totalEffect.translateZ += rippleStrength * 20;
    }

    const energyFactor = this.state.energy / this.MAX_ENERGY;
    totalEffect.scale *= 1 + energyFactor * 0.1;
    totalEffect.blur *= energyFactor;
    totalEffect.brightness *= 1 + energyFactor * 0.2;

    return totalEffect;
  }

  createBreathingAnimation(element: HTMLElement, itemId: string) {
    const timeline = gsap.timeline({ repeat: -1 });
    this.updateSpatialGrid(element, itemId);

    const updateBreathing = () => {
      const effects = this.calculateRippleEffect(element);
      const energyFactor = this.state.energy / this.MAX_ENERGY;
      const breathingIntensity = this.BREATHING_INTENSITY * (1 + energyFactor);

      timeline
        .to(element, {
          scale: effects.scale * (1 + breathingIntensity),
          rotation: effects.rotation,
          filter: `blur(${effects.blur}px) brightness(${effects.brightness})`,
          transform: `perspective(1000px) translateZ(${effects.translateZ}px)`,
          duration: 2,
          ease: 'energyPulse',
        })
        .to(element, {
          scale: effects.scale,
          rotation: 0,
          filter: `blur(0px) brightness(1)`,
          transform: 'perspective(1000px) translateZ(0)',
          duration: 2,
          ease: 'rippleOut',
        });
    };

    gsap.ticker.add(updateBreathing);
    return timeline;
  }

  createHoverAnimation(element: HTMLElement, itemId: string) {
    this.state.activeItems.add(itemId);
    this.addEnergy();

    const energy = this.state.energy;
    const intensity = energy / this.MAX_ENERGY;
    const rippleEffect = this.calculateRippleEffect(element);

    return gsap.to(element, {
      scale: (1.05 + intensity * 0.05) * rippleEffect.scale,
      rotationX: Math.random() * 10 - 5,
      rotationY: Math.random() * 10 - 5,
      rotation: rippleEffect.rotation,
      duration: 0.3,
      ease: 'power2.out',
      onComplete: () => {
        this.state.activeItems.delete(itemId);
      },
    });
  }

  createProximityAnimation(
    element: HTMLElement,
    itemId: string,
    x: number,
    y: number
  ) {
    const rect = element.getBoundingClientRect();
    const globalX = rect.left + (x * rect.width) / 100;
    const globalY = rect.top + (y * rect.height) / 100;

    // Add a weak ripple effect for proximity
    this.addRipple(globalX, globalY, 0.3);

    const distance = Math.hypot(x - 50, y - 50);
    const intensity = 1 - distance / 150;
    const energy = this.state.energy / this.MAX_ENERGY;
    const rippleEffect = this.calculateRippleEffect(element);

    return gsap.to(element, {
      duration: 0.5,
      rotationX: (y - 50) * 0.1 * intensity * energy,
      rotationY: (x - 50) * 0.1 * intensity * energy,
      scale: rippleEffect.scale * (1 + 0.05 * intensity * energy),
      rotation: rippleEffect.rotation,
      ease: 'power2.out',
      transformOrigin: 'center center',
    });
  }

  createMorphAnimation(element: HTMLElement, itemId: string) {
    const energy = this.state.energy;
    const intensity = energy / this.MAX_ENERGY;
    const rippleEffect = this.calculateRippleEffect(element);

    return gsap.to(element, {
      duration: 0.5,
      scale: rippleEffect.scale,
      rotation: rippleEffect.rotation,
      borderRadius: `${8 + intensity * 12}px`,
      boxShadow: `0 ${4 + intensity * 8}px ${
        12 + intensity * 16
      }px rgba(0,0,0,${0.1 + intensity * 0.1})`,
      ease: 'power2.out',
    });
  }

  private updateSpatialGrid(element: HTMLElement, itemId: string) {
    const rect = element.getBoundingClientRect();
    const gridX = Math.floor(rect.left / this.GRID_SIZE);
    const gridY = Math.floor(rect.top / this.GRID_SIZE);
    const gridKey = `${gridX},${gridY}`;

    // Remove from old grid positions
    this.state.spatialGrid.forEach((items, key) => {
      items.delete(itemId);
      if (items.size === 0) {
        this.state.spatialGrid.delete(key);
      }
    });

    // Add to new grid position
    if (!this.state.spatialGrid.has(gridKey)) {
      this.state.spatialGrid.set(gridKey, new Set());
    }
    this.state.spatialGrid.get(gridKey)!.add(itemId);
  }

  private getNearbyItems(point: Point): string[] {
    const gridX = Math.floor(point.x / this.GRID_SIZE);
    const gridY = Math.floor(point.y / this.GRID_SIZE);
    const nearby = new Set<string>();

    // Check surrounding grid cells
    for (let dx = -1; dx <= 1; dx++) {
      for (let dy = -1; dy <= 1; dy++) {
        const key = `${gridX + dx},${gridY + dy}`;
        const items = this.state.spatialGrid.get(key);
        if (items) {
          items.forEach((id) => nearby.add(id));
        }
      }
    }

    return Array.from(nearby);
  }

  addRipple(x: number, y: number, strength: number = 1) {
    const rippleId = Date.now().toString();
    if (this.state.ripples.size >= this.MAX_RIPPLES) {
      // Remove oldest ripple
      const oldest = Array.from(this.state.ripples.entries()).sort(
        ([, a], [, b]) => a.timestamp - b.timestamp
      )[0][0];
      this.state.ripples.delete(oldest);
    }

    this.state.ripples.set(rippleId, {
      x,
      y,
      strength,
      timestamp: Date.now(),
    });
  }

  updateColorEnergy() {
    const hue = (this.state.colorEnergy * 360) % 360;
    document.documentElement.style.setProperty('--energy-hue', hue.toString());
    this.state.colorEnergy += 0.001 * (this.state.energy / this.MAX_ENERGY);
  }

  getRipples(): Array<{
    x: number;
    y: number;
    strength: number;
    timestamp: number;
  }> {
    return Array.from(this.state.ripples.values());
  }
}

export default AnimationSystem;
